import config from './dev.env';

export const saveToDatabase = async (data) => {
    const { BASE_URL } = config;
    
    const configs = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
    };

    try {
        const response = await fetch(`${BASE_URL}/send`, configs);
        const res = response.json();
        return res;
    } catch (error) {
        console.error(error);
        return;
    }
}