import React from 'react';
import * as yup from 'yup';
import { Form, Formik } from 'formik';
import moment from 'moment';
import TextInput from './MyTextInput';
import CustomButton from './CustomButton';
import { saveToDatabase } from '../utils';

const formSchema = yup.object({
    firstName: yup.string()
        .max(20, 'Must be 20 characters or less')
        .required('Required'),
    middleName: yup.string()
        .max(20, 'Must be 20 characters or less'),
    lastName: yup.string()
        .max(20, 'Must be 20 characters or less')
        .required('Required'),
    email: yup.string()
        .email('Invalid email address')
        .required('Required'),
});

const handleSubmit = async (values) => {
    const date = moment().format();
    const saveToDatabaseRes = await saveToDatabase({...values, date: date});
    console.log('saveToDatabaseRes:', saveToDatabaseRes);
    alert(`${saveToDatabaseRes.message}`);
    return;
}

const SignUpForm = () => {
    return (
        <Formik
            initialValues={{ firstName: '', middleName: '', lastName: '', email: '' }}
            validationSchema={formSchema}
            onSubmit={(values, {resetForm}) => {
                handleSubmit(values);
                resetForm();
            }}
        >
            <Form>
                <TextInput 
                    name="firstName"
                    type="text"
                    placeholder="First"
                />
                <TextInput 
                    name="middleName"
                    type="text"
                    placeholder="Middle"
                />
                <TextInput 
                    name="lastName"
                    type="text"
                    placeholder="Last"
                />
                <TextInput 
                    name="email"
                    type="email"
                    placeholder="Email"
                />
                <CustomButton title="Submit" type="submit" />
            </Form>
        </Formik>
    )
}

export default SignUpForm;